<template>
  <div class="row p-4 text-center">
      <loading-indicator v-if="isLoading"></loading-indicator>
    <div class="col-sm-12 col-md-8 offset col-xs-12 mobile-login-form" v-if="!isLoading">
      <form @submit.prevent="handleLogin()">
        <div class="form-group">
          <label for="phone">Phone Number</label>
          <input type="text" class="form-control rounded-0"
                 v-model="authData.phone"
                 required
                 id="phone" aria-describedby="phoneHelp" placeholder="Enter Phone Number">
          <small id="phoneHelp" class="form-text text-muted">

          </small>
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input type="password" class="form-control"
                 v-model="authData.password"
                 required
                 id="password" placeholder="Password">
        </div>
        <button type="submit" class="btn btn-primary mr-2 button-form rounded-0 btn-block"
                :disabled="!authData.phone || !authData.password">
            Login
        </button>
      </form>
      <br>
      <a @click="()=>{$router.push({name:'reset_password'})}" class="float-right">Forgot Password?Reset</a>
      <br>
      <br>
      <button @click="()=>{$router.push({name:'register'})}" class="btn custom-login-button btn-block">Register</button>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  import {LOGIN_URL} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import LoadingIndicator from "../Utilities/LoadingIndicator";

  export default {
    name: 'LoginForm',
      components: {LoadingIndicator},
      data () {
      return {
        opt: false,
        submitted: false,
          isLoading: false,
        authData: {
          phone: '',
          password: '',
          client_id: AUTH_CONFIG.CLIENT_ID,
          client_secret: AUTH_CONFIG.CLIENT_SECRET,
          grant_type: AUTH_CONFIG.GRANT_TYPE
        },
        loginResponseData: null
      }
    },
    methods: {
      handleLogin () {
        this.submitted = true
          this.isLoading = true
        axios.post(LOGIN_URL, this.authData).then((response) => {
          let loginResponseData = response.data
          if (loginResponseData.code === 200) {
            let token = loginResponseData.token
            window.localStorage.setItem('token', JSON.stringify(token))
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
            this.$router.push({name: 'home'})
            this.$noty.success(loginResponseData.message)
            this.isLoading = false
          } else {
            this.$noty.error('Unable to log you in. Please check to make sure the phone number and password combination is correct')
            this.isLoading = false
          }
        }, (error) => {
          let responseError = error.response.data
          let message = responseError.errors.phone ? responseError.errors.phone[0] : ''
            this.isLoading = false
          this.submitted = false
          this.$noty.error(responseError.message + '<br />' + message)
        })
      }
    }
  }
</script>
<style scoped lang="stylus">
  .mobile-login-form
    border 1px solid #464646
    padding 23px
    box-shadow 0 0 20px 5px rgb(0, 0, 0)

  .custom-login-button
    color #060f2d
    font-size 17px
    background-color #bdd541
    border-color #bdd541
</style>
